"use client"

import { useState } from "react"
import { Book, getBooks, addBook, updateBook, deleteBook } from "@/lib/books"
import { Button } from "@/components/ui/button"
import { BookForm } from "@/components/BookForm"
import { Dialog, DialogContent, DialogHeader, DialogTitle } from "@/components/ui/dialog"
import { AlertDialog, AlertDialogAction, AlertDialogCancel, AlertDialogContent, AlertDialogDescription, AlertDialogFooter, AlertDialogHeader, AlertDialogTitle } from "@/components/ui/alert-dialog"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { useToast } from "@/components/ui/use-toast"
import { Toaster } from "@/components/ui/toaster"

export default function AdminDashboard() {
  const [books, setBooks] = useState(getBooks())
  const [isFormOpen, setIsFormOpen] = useState(false)
  const [selectedBook, setSelectedBook] = useState<Book | null>(null)
  const [bookToDelete, setBookToDelete] = useState<Book | null>(null)
  const { toast } = useToast()

  const handleAddBook = (bookData: Omit<Book, "id">) => {
    const newBook = addBook(bookData)
    setBooks(getBooks())
    setIsFormOpen(false)
    toast({
      title: "Success",
      description: "Book added successfully",
    })
  }

  const handleUpdateBook = (bookData: Omit<Book, "id">) => {
    if (selectedBook) {
      updateBook(selectedBook.id, bookData)
      setBooks(getBooks())
      setIsFormOpen(false)
      setSelectedBook(null)
      toast({
        title: "Success",
        description: "Book updated successfully",
      })
    }
  }

  const handleDeleteConfirm = () => {
    if (bookToDelete) {
      deleteBook(bookToDelete.id)
      setBooks(getBooks())
      setBookToDelete(null)
      toast({
        title: "Success",
        description: "Book deleted successfully",
      })
    }
  }

  const openEditForm = (book: Book) => {
    setSelectedBook(book)
    setIsFormOpen(true)
  }

  return (
    <div className="min-h-screen bg-gray-50 py-8">
      <div className="max-w-7xl mx-auto px-4">
        <div className="flex justify-between items-center mb-8">
          <h1 className="text-3xl font-bold text-gray-900">Admin Dashboard</h1>
          <Button onClick={() => setIsFormOpen(true)}>Add New Book</Button>
        </div>

        <div className="bg-white rounded-lg shadow">
          <Table>
            <TableHeader>
              <TableRow>
                <TableHead>Title</TableHead>
                <TableHead>Author</TableHead>
                <TableHead>Category</TableHead>
                <TableHead>Status</TableHead>
                <TableHead className="text-right">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {books.map((book) => (
                <TableRow key={book.id}>
                  <TableCell className="font-medium">{book.title}</TableCell>
                  <TableCell>{book.author}</TableCell>
                  <TableCell>{book.category}</TableCell>
                  <TableCell>
                    <span className={`inline-block px-2 py-1 rounded-full text-xs ${
                      book.available ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'
                    }`}>
                      {book.available ? 'Available' : 'Borrowed'}
                    </span>
                  </TableCell>
                  <TableCell className="text-right">
                    <Button
                      variant="outline"
                      size="sm"
                      className="mr-2"
                      onClick={() => openEditForm(book)}
                    >
                      Edit
                    </Button>
                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={() => setBookToDelete(book)}
                    >
                      Delete
                    </Button>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>

        {/* Add/Edit Book Dialog */}
        <Dialog open={isFormOpen} onOpenChange={setIsFormOpen}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>{selectedBook ? 'Edit Book' : 'Add New Book'}</DialogTitle>
            </DialogHeader>
            <BookForm
              book={selectedBook || undefined}
              onSubmit={selectedBook ? handleUpdateBook : handleAddBook}
              onCancel={() => {
                setIsFormOpen(false)
                setSelectedBook(null)
              }}
            />
          </DialogContent>
        </Dialog>

        {/* Delete Confirmation Dialog */}
        <AlertDialog open={!!bookToDelete} onOpenChange={() => setBookToDelete(null)}>
          <AlertDialogContent>
            <AlertDialogHeader>
              <AlertDialogTitle>Are you sure?</AlertDialogTitle>
              <AlertDialogDescription>
                This action cannot be undone. This will permanently delete the book
                "{bookToDelete?.title}" from the library system.
              </AlertDialogDescription>
            </AlertDialogHeader>
            <AlertDialogFooter>
              <AlertDialogCancel>Cancel</AlertDialogCancel>
              <AlertDialogAction onClick={handleDeleteConfirm}>Delete</AlertDialogAction>
            </AlertDialogFooter>
          </AlertDialogContent>
        </AlertDialog>

        <Toaster />
      </div>
    </div>
  )
}
